import React, { useState } from "react";
import { Link } from "react-router-dom";
import Login from "./Login";
import "../App.css";

function SignUp() {
  const [username, setUser] = useState("");
  const [password, setPassword] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [confirm, setConfirm] = useState("");


  const details = () => {

    console.log(username);
  };

  return (
    <div className="App">
      <h1 className="App-header">Sign Up</h1>
      <form action="">
        <div className="input">
          <label className="labels" htmlFor="username"> Username:</label>
          <input
            className="input-fields"
            type="text"
            id="username"
            minLength={6}
            placeholder="Enter your username"
            required
            autoComplete="off"
            onChange={(e) => {
              setUser(e.target.value);
            }}
            value={username}
          />
        </div>
        <div className="input">
          <label className="labels" htmlFor="email">
            Email Address:{" "}
          </label>
          <input
            className="input-fields"
            type="email" //implement email validation
            id="email"
            placeholder="Enter your email ID"
            required
            autoComplete="off"
            onChange={(e) => {
              setEmail(e.target.value);
            }}
            value={email}
          />
        </div>
        <div className="input">
          <label className="labels" htmlFor="phone">
            Phone:
          </label>
          <input
            className="input-fields"
            type="phone"
            id="phone"
            placeholder="Enter your phone no."  //phone no. validation
            required

            autoComplete="off"
            onChange={(e) => {
              setPhone(e.target.value);
            }}
            value={phone}
          />
        </div>

        <div className="input">
          <label className="labels" htmlFor="password">
            Password:{" "}
          </label>
          <input
            className="input-fields"
            type="password"
            id="password"
            placeholder="Create a password" //match password to confirm
            autoComplete="off"
            required
            onChange={(e) => {
              setPassword(e.target.value);
            }}
            value={password}
          />
        </div>
        <div className="input">
          <label className="labels" htmlFor="confirm">
            {" "}
            Confirm Password:{" "}
          </label>
          <input
            className="input-fields"
            type="password"
            id="confirm"
            placeholder="Confirm your password"
            required
            autoComplete="off"
            onChange={(e) => {
              setConfirm(e.target.value);
            }}
            value={confirm}
          />
        </div>
        <button className="btn" onClick={details}>
          Sign Up
        </button>
      </form>
      <p className="para">
        Already have an account?{" "}
        <Link to="/login" className="App-link">
          Log In
        </Link>
      </p>

    </div>
  );
}

export default SignUp;

